.nav{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  min-height: 60px;
  border-bottom: 1px solid #ddd;
  box-shadow: 1px .5px 3px;
}

.nav ul {
  display: flex;
  align-items: center;
  list-style: none;
}

.nav ul li {
  margin-right: 24px;
}